import { NextResponse } from "next/server";
import { v4 as uuidv4 } from "uuid";

const BASE_URL = "https://api.ecoledirecte.com/";

export async function POST() {
  try {
    const loginRes = await fetch(`${BASE_URL}/login.awp?v=4.75.0`, {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
        "User-Agent":
          "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 Chrome/139.0.0.0 Safari/537.36",
      },
      body:
        "data=" +
        encodeURIComponent(
          JSON.stringify({
            identifiant: process.env.ECOLE_DIRECTE_USER,
            motdepasse: process.env.ECOLE_DIRECTE_PASS,
            isRelogin: false,
            uuid: uuidv4(),
          })
        ),
    });

    const data = await loginRes.json();

    // Vérifier si un compte est renvoyé
    if (data.token && data.data.accounts && data.data.accounts.length > 0) {
      return NextResponse.json({
        message: "Login réussi !",
        token: data.token,
        accounts: data.data.accounts.map((acc: any) => ({
          id: acc.id,
          typeCompte: acc.typeCompte,
          identifiant: acc.identifiant,
          lastConnexion: acc.lastConnexion,
        })),
      });
    } else {
      return NextResponse.json(
        { error: "Échec login", details: data },
        { status: 401 }
      );
    }
  } catch (e) {
    return NextResponse.json(
      { error: "Erreur API", details: String(e) },
      { status: 500 }
    );
  }
}
